@import "../../util/colors";
@import "../../util/fonts";
@import "../../util/input";

.teams-settings {
    padding: 0 24px 24px;

    .teams-settings-section {
        position: relative;
        width: 500px;
        max-width: 90%;
        margin: 1em auto 1em;
        padding: 0 0 8px;
        background: #eee;
        box-shadow: 0 8px 0 -4px  rgba(0, 0, 0, .25);

        .header {
            @include styled-button(false);
            text-transform: uppercase;
            text-align: left;
            padding: 16px;
        }

        .field-name {
            display: block;
            padding: 16px 16px 0 16px;
            font-family: $font-family-text;
            font-size: 1em;
            text-align: left;
            line-height: 1.2em;
            text-shadow: 0 1px 0 #fff;
        }

        .field-name--bold {
            font-family: $font-family-title;

        }

        .field-content {
            position: relative;

            .field-input {
                @include styled-button(false);
                background: #eee;
                padding: 8px 16px;
                position: relative;
                box-sizing: border-box;

                &.field-input-small {
                    padding-right: 64px;
                }

                &.field-input-large {
                    padding-right: 96px;
                }

                .field-checkbox {
                    font-size: 16px;
                    color: #000;
                    text-shadow: 0 1px 0 #fff;
                }

                .field-slider {
                    //$track-width, $track-height, $track-background, $track-box-shadow, $track-border-radius,
                    //$thumb-size, $thumb-background, $thumb-box-shadow, $thumb-border, $thumb-border-radius
                    @include input-range(
                        100%, 8px, #333, 0 1px 0 #fff, 1px,
                        24px, -8px, #38c, 0 4px 0 -2px  rgba(0, 0, 0, .25), none, 0
                    );

                    width: 100%;
                }
            }

            .field-description {
                box-sizing: border-box;
                padding: 8px 64px 8px 32px;
                font-family: $font-family-text;
                font-size: 1em;
                text-align: left;
                line-height: 1.0em;
                color: #555;
                text-shadow: 0 1px 0 #fff;
            }

            .field-value {
                position: absolute;
                right: 0;
                bottom: 0;
                padding: 8px 16px 8px 0;
                color: #000;
                font-family: Hanzel, Verdana, Geneva, sans-serif;
                font-size: 1em;
                text-shadow: 0 1px 0 #fff;
            }
        }
    }

    .clear {
        height: 32px;
    }
}



