@import "../util/colors";
@import "../util/fonts";

.message {
    width: 500px;
    max-width: 90%;
    padding: 16px;
    margin: 0 auto 1em;
    background: rgba(0, 0, 0, .5);
    color: #fff;
    font-family: $font-family-text;
    font-size: 1em;
    text-shadow: 0 1px 0 #000;
    text-align: center;

    .message-alt {
        color: #ccc;
    }
}



